.sub-nav-bar-left {
    display: flex;
    flex-direction: column;
}

.sub-nav-bar-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.sub-nav-bar {
    padding-top: 32px;
    row-gap: 20px;
    margin-left: 10px;

    &-item {
        width: 100%;

        &-name {
            background-color: #E2BFB3;
            height: 44px;
            line-height: 44px;
            color: #000;
            font-size: 18px;
            font-weight: 600;
            padding-left: 16px;
            border-radius: 15px 15px 0 0;
        }

        &-content {
            background-color: #F5E8DD;
            min-height: 420px;
            padding: 0 8px 8px 8px;
            border-radius: 0 0 15px 15px;

            &-folder-name {
                display: flex;
                align-items: center;
                column-gap: 12px;
                padding-top: 8px;
            }

            &-folder-grade {
                display: flex;
                align-items: center;
                column-gap: 12px;
                padding: 8px 0 0 16px;
            }

            &-folder-course {
                display: flex;
                align-items: center;
                column-gap: 12px;
                padding: 8px 0 0 32px;
            }
        }

    }
}